import { Button, PopoutWrapper } from "@vkontakte/vkui";
import React, { useState, useEffect } from 'react';
import Berpie from "../../../img/gif/uprazhnenie-berpi-vashsport.com_.gif"
import Podem_ruki from "../../../img/ShouldersGif/Podem_ruki_pered_soboj.gif"
import Razvedenie from "../../../img/ShouldersGif/Razvedenie_ruk_v_naklone.gif"


const Timer = () => {
  const [time, setTime] = useState(25 * 60);
  
  const [isRunning, setIsRunning] = useState(false);

  const [currentImage, setCurrentImage] = useState(Podem_ruki);

  const [count, setCount] = useState(0);

  useEffect(() => {
    if (time <= 25 * 60 && time >= 20 * 60) {
      setCurrentImage(Podem_ruki);
      setCount(setCount + 1);
    } else if (time <= 20 * 60 && time >= 15 * 60) {
      setCurrentImage(Razvedenie);
      setCount(setCount + 1);
    } else if (time <= 15 * 60 && time >= 10 * 60) {
      setCurrentImage(Berpie);
      setCount(setCount + 1);
    } else if (time <= 10 * 60 && time >= 5 * 60) {
      setCurrentImage(Berpie);
      setCount(setCount + 1);
    } else if (time <= 5 * 60 && time >= 0 * 60) {
      setCurrentImage(Berpie);
      setCount(setCount + 1);
    }
  }, [time]);

  useEffect(() => {
    let interval;
    if (isRunning && time > 0) {
      interval = setInterval(() => {
        setTime((prevTime) => prevTime - 1);
      }, 1000);
    }
    return () => clearInterval(interval);
  }, [isRunning, time]);

  const startTimer = () => {
    setIsRunning(true);
  };

  const stopTimer = () => {
    setIsRunning(false);
  };

  const resetTimer = () => {
    setTime(25 * 60);
  };

  const formatTime = () => {
    const minutes = Math.floor(time / 60).toString().padStart(2, '0');
    const seconds = (time % 60).toString().padStart(2, '0');
    return `${minutes}:${seconds}`;
  };

  return (
    <div style={{display: "flex",
    alignItems:"center",
    height:"500",
    minWidth:"400",
    flexWrap:"wrap",
    marginLeft:"auto",
    marginRight:"auto",
    flexDirection: "column",
    }}> 
    
     <img src={currentImage} alt="" style={{
      width:350,
      height:350,
     }} />
      <h1 style={{
        minWidth:"400"
      }}>: {formatTime()}</h1>
      {time <= 25*60 && time >= 20*60 && <h2 style={{textAlign:"center"}}> :    ()</h2>}
      {time <= 20*60 && time >= 199*6 && <p>  2 </p>}
      {time <= 20*60 && time >= 15*60 && <h2 style={{textAlign:"center"}}> :   ()</h2>}
      {time <= 15*60 && time >= 149*6 && <p>  3 </p>}
      {time <= 15*60 && time >= 10*60 && <h2 style={{textAlign:"center"}}> :      ()</h2>}      
      {time <= 10*60 && time >= 99*6 && <p>  4 </p>}
      {time <= 10*60 && time >= 5*60 && <h2 style={{textAlign:"center"}}> :  </h2>}      
      {time <= 5*60 && time >= 49*6 && <p>  5 </p>}
      {time <= 5*60 && time >= 0*60 && <h2 style={{textAlign:"center"}}> :  ()  </h2>}        
      {time === 0 && <p> !</p>}  {/*   0,   " !"*/}  
      <div style={{
        display:"flex",
        justifyContent:"space-between",
        minWidth:"400",
        
      }}>
      
      <div style={{
        marginRight:50
      }}>
         {!isRunning && time > 0 && (
        <Button onClick={startTimer}></Button> //  ,        0
      )}
      {isRunning && (
        <Button onClick={stopTimer}></Button> //  ,   
      )}  
      </div>
      <div>
      <Button onClick={resetTimer}></Button>  {/* */}        
      </div>


      </div>
     
    </div>
  );
};

const OpenShoulder = ({ onClose }) => {
  return (
    <PopoutWrapper onClick={onClose}>
      <div style={{
        backgroundColor: "var(--vkui--color_background_content)",
        borderRadius: 8,
        position: "relative",
        padding: "12px",
        width: "500px",

      }}>
        <div style={{
        display: "flex",
        alignItems:"center",
        
        }}>
          
          <Timer />
          {/* <Button onClick={onClose}></Button> */}
        </div>
      </div>
    </PopoutWrapper>
  );
};

export default OpenShoulder;