import React, { useState } from "react";
import { Group, Button, Header } from "@vkontakte/vkui";
import "./Multiblok.css";
import Lesson1 from "../../../img/Chest.png";
const FullBody = ({ go }) => {
  return (
    <Group
      className="MainGroup"
      separator="show"
      header={<Header mode="primary" className="marginMain">   </Header>}
    >
      <div className="up_panel">
        <div className="margin">
          <p>: 7</p>
        </div>
        <div>
          <img
            className="musckle_img musckle_img_border"
            src={Lesson1}
            alt="Lesson1"
          />
        </div>
      </div>

      <div className="margin">
        <p></p>
        <p>   </p>
        <p> </p>
      </div>

      <div className="down_panel">
        <Button onClick={go} data-to="popout" className="MainBt">
           
        </Button>
      </div>
    </Group>
  );
};

export default FullBody;

